.signup-page {
    height: 360px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.signup-page > img {
    width: 70px;
    height: 70px;
    object-fit: cover;
    margin: 20px;
}

.signup-page > form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.signup-page > form > * {
    margin-bottom: 10px;
}

.signup-page > form > input[type="text"] {
    padding: 3px 7px;
}
.signup-page > form > input[type="submit"] {
    box-shadow: 0 0 2px #0000009e;
    margin: 0;
    padding: 3px 10px;
}



.moves-list {
    background-color: #eee;
    padding: 1px 20px 10px 20px;
    border-radius: 7px;
}

.home-page {
  max-height: 360px;
  overflow-y: scroll;
}
.user-details {
  padding: 20px;
  margin: 0 0 20px 0;
}

.user-name {
  margin: 0 0 20px 0;
  font-size: 22px;
}

.user-coins-count {
  margin: 10px 0 0 0;
  display: flex;
  align-items: center;
}

.user-coins-rate {
  margin: 10px 0 0 0;
  display: flex;
  align-items: center;
}

.user-coins-count img, .user-coins-rate img {
  margin: 0 5px 0 0;
}


.chart {
  margin: 20px 0;
}

.chart-name {
  margin: 5px 0;
}

.chart-desc {
  font-size: 0.8rem;
}
.statistic-chart:not(:last-child) {
  margin: 0 0 20px 0;
  border-bottom: 1px solid #f4f5f7;
}
.contact-preview {
  display: inline-block;
  padding: 0 25px;
  height: 50px;
  width: 240px;
  font-size: 17px;
  line-height: 50px;
  border-radius: 25px;
  background-color: #f1f1f1; 
}
  
.contact-preview img {
  float: left;
  margin: 0 10px 0 -25px;
  height: 50px;
  width: 50px;
  border-radius: 50%; 
}

.contacts-list-item {
  margin: 10px 0; 
}

.contacts-page {
  position: relative;
  height: 100%; 
}

.contacts-page .search-container {
  height: 40px;
  padding: 0 10px; 
}

.contacts-page .contacts-container {
  max-height: 360px;
  overflow-x: hidden; 
}

.contacts-page .action-container {
  position: absolute;
  bottom: -3px;
  right: 10px;
}

.add-new-img {
  position: absolute;
  height: 55px;
  right: 15px;
  bottom: -5px;
}
.transfer-fund {
    background-color: #afd6fb;
    padding: 1px 10px 20px 10px;
    border-radius: 7px;
    margin: 20px 0;
}

input[type="number"] {
    width: 80px;
    background: none;
    border: none;
    border-bottom: 1px solid gray;
}

input[type="submit"] {
    cursor: pointer;
    background: none;
    border: none;
    background-color: #ffffffb8;
    border-radius: 3px;
    margin-left: 10px;
    padding: 2px 8px;
}
.contact-details {
  height: 360px;
  overflow-y: scroll;
}

.contact-details-header {
  display: flex;
  justify-content: space-between;
  height: 40px; 
}
.contact-details-header a:not(:last-child) {
  margin: 0 10px 0 0; 
}

.contact-details .contact-details-row {
  margin: 10px 0 0 0; 
}

.moves-list-container, .transter-coins-container {
  margin: 20px 0;
}

.avatat-img {
  display: block;
  width: 96px;
  height: 96px; 
}

.datails-btn {
  background: none;
  border: none;
  width: 35px;
  height: 35px;
  padding: 1px;
}

.datails-btn:last-of-type {
  float: right;
}
.contact-edit-header {
    display: flex;
    justify-content: space-between;
    height: 40px; 
  }
  
  .moves-list-container, .transter-coins-container {
    margin: 20px 0;
  }

  .edit-form {
    margin-bottom: 60px;
    display: grid;
    grid-template-columns: 55px 1fr;
    grid-template-rows: 40px 40px 40px 40px;
    align-items: center;
  }

  .edit-form > input {
    padding: 5px 10px;
    border: .5px solid gray;
    border-radius: 3px;
  }

  .edit-btn {
    background: none;
    border: none;
    width: 35px;
    height: 35px;
    padding: 1px;
  }
  
  .edit-btn:last-of-type {
    float: right;
  }

  .save-btn {
    float: right;
    background: none;
    border: none;
    cursor: pointer;
    border-radius: 4px;
    box-shadow: 0 0 2px 0px #0000007d;
    padding: 3px 10px;
    grid-column: 2;
    width: 50px;
    justify-self: end;
    align-self: end;
  }
* {
    box-sizing: border-box;
}

nav {
    border-bottom: 1px solid black;
    height: 45px;
    align-items: center;
    justify-content: space-around;
    display: flex;
    padding-bottom: 7px;
}

a {
    height: 100%;
}

img {
    height: 100%;
    object-fit: cover;
    padding: 5px;
}

.nav-link {
    border-radius: 5px;
    -webkit-transition: .3s;
    transition: .3s;
}

.active-route {
    /* background-color: #76bbf1; */
    /* background-color: #cac3c3; */
    background-color: #f1f1f1;
}
.app {
  max-width: 300px;
  padding: 20px;
  border: 1px solid black;
  border-radius: 5px;
  margin: 30px auto; 
}

.app [class^="flaticon-"]:before,
.app [class*=" flaticon-"]:before,
.app [class^="flaticon-"]:after,
.app [class*=" flaticon-"]:after {
    margin: 0; 
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 15px; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

body {
    margin: 0;
    padding: 0;
}

a, a:visited, a:hover, a:active {
    text-decoration: none;
    color: inherit; 
}

ul {
    margin: 0;
    padding: 0; 
}
  
li {
    list-style: none; 
}
